.spinner {
    width: 150px;
    margin: 0 auto;
    -webkit-animation: pulse 1.5s infinite;
            animation: pulse 1.5s infinite;
    -webkit-animation-name: pulse;
            animation-name: pulse;
}

.spinner img {
    width: 150%;
}


@-webkit-keyframes pulse {
    0% {
        transform: scale(.7);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(.7);
    }
}


@keyframes pulse {
    0% {
        transform: scale(.7);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(.7);
    }
}
